.SidebarButton-tooltip {
  position: relative;
  display: inline-block;
  margin: 10px auto;
}

.SidebarButton-tooltip .SidebarButton-tooltiptext {
  background-color: black;
  color: #fff;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  padding: 3px 1px;
  border-radius: 6px;
  position: absolute;
  z-index: 1;
  width: 75px;
  height: 20px;
  bottom: 75%;
  left: 50%;
  opacity: 0;
}

.SidebarButton-tooltip:hover .SidebarButton-tooltiptext {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 2s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: opacity 2s cubic-bezier(0.075, 0.82, 0.165, 1);
  -webkit-transition-delay: 2s;
          transition-delay: 2s;
}

.SidebarButton-tooltip:hover .SidebarButton-tooltiptext::after {
  content: '';
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 20%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

